import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: '首页',
      component: () => import('@/pages/home'),
      redirect: '/video',

      children:[
        {
          path: '/video',
          component: () => import('@/components/video/video')
        },

        {
          path:'/announce',
          component: ()=>import('@/components/announce/announce')
        }

      ]
    },

    {
      path: '/videoDetail',
      name: '视频详情页',
      component: () => import('@/pages/video/detail')
    },

    {
      path: '/announceDetail',
      name: '公告详情页',
      component: () => import('@/pages/announce/detail')
    }
  ]
})



